/*JUMBOTRON*/
.jumbotron {
	padding: 0px !important;
    background-blend-mode: lighten;
    background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	margin-bottom: 0px;
    text-align: center;
}

.jumbotron h1 {
	font-family: 'Montserrat', sans-serif;
	font-size: 65px;
	text-transform: uppercase;
	font-weight: 900;
	color: #e7283a;
}

.jumbotron p {
	margin-bottom: 0px !important;
	font-family: 'Montserrat', sans-serif;
	font-size: 18px;
	font-weight: 500;
	text-transform: uppercase;
}
.allCards {
    padding: 10px;
    margin-bottom: 10px;
    border: none;
    text-align: center;
}

.allCards img {
    margin: 5px;
    padding: 20px;
    border: 1px solid rgba(0, 0, 0, 0.25);
    max-height: 200px;
    max-width: 200px;
    background-color: white;
}
.mainCardContainer {
    
    padding: 0; 
}

.gameMessage {
    text-align: center;
    max-width: 100%;
    align-content: center
}

.gameMessage p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color: black;
    color: white;
    padding: 20px 0;
    margin-bottom: 0px;
}

.gameScores {
    text-align: center;
    max-width: 100%;
    align-content: center;
    margin-bottom: 10px;
}
.gameScores p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color:#e7283a;
    color: white;
    padding: 20px 0;
    margin-bottom: 25px;
}
.mainContainer {
padding: 0; 
background-color: rgba(0, 0, 0, 0.03);
min-height: 100vh;
padding-bottom: 100px;
}
